<template>
  <div>
    <div
      v-show="loading"
      class="m-loading-header"
    >
      <res-icon class="m-loading-icon" name="loading" size="large" />
      <p class="m-loading-text">Loading entitlements</p>
    </div>
    <entitlements-table :entitlements="entitlements"/>
  </div>
</template>

<script>
import EntitlementsTable from '../../../components/entitlements-table.vue';
import { getEntitlements } from '../../../actions/entitlements';

export default {
  name: 'Dashboard',
  components: {
    EntitlementsTable
  },
  data() {
    return {
      entitlements: [],
      loading: true
    };
  },
  async created() {
    this.entitlements = await getEntitlements();
    this.loading = false;
  }
};
</script>
