import graphqlRequest from '../../client/lib/graphql_request';

export default async function updateRedirect(redirectId, source, destination, community, status) {
  await graphqlRequest('/api/tower/graphql', {
    query: `
      mutation updateRedirect($redirectId: Int!, $path: String!, $destination: String!, $communityId: Int!, $status: Int!) {
        updateRedirect(where: {id: $redirectId}, input: {path: $path, destination: $destination, community: { id: $communityId }, status: $status}) {
          redirect {
            id
          }
        }
      }
    `,
    variables: JSON.stringify({ redirectId: redirectId, path: source, destination: destination, communityId: community, status: status })
  });
  return;
}
