<template>
  <div class="m-redirects__form">
    <div class="m-redirects__messaging">
      <res-banner v-show="creatingRedirect"><span>Creating redirect...</span></res-banner>
      <res-banner v-show="redirectCreated" design="positive"><span>Success! Your redirect was {{ saveContext }}.</span></res-banner>
      <res-banner v-show="createError" design="negative"><span>Error: {{ saveErrorMessage }}</span></res-banner>
    </div>
    <div class="l-content">

      <main class="m-dashboard__content">
        <div class="m-dashboard__sub-head">
          <div class="m-dashboard__sub-head__title">
            <res-button type="default" paddingless>
              <res-icon size="small" name="back"/>
              <router-link :to="{ path: '/admin/redirects', query: { community_id: newDomain, network_id: newNetwork }}">Back to redirects</router-link>
            </res-button>
            <h1>{{ editContext }} redirect</h1>
          </div>
        </div>
        <div class="m-edit">


          <networkFilter
            :networks="networks"
            :networkId="networkId"
            :communityId="newDomain"
            @community-changed="updateCommunityAndNetworkId"
          />


          <div class="m-edit-redirect"><res-input><label>Redirect from</label> <input v-model="newPath" type="text" name="redirects-from" value=""/>
            <div slot="help" class="res-input__help-text">
              <p>This URL path should not include a domain and must begin with a slash, eg. <em>/about</em></p>
              <p>The full URL you're redirecting from is <strong>http://{{ newDomainName }}{{ newPath }}</strong></p>
            </div>
          </res-input>
          </div>

          <div class="m-edit-redirect">
            <res-input>
              <label>Redirect to</label><input v-model="newDestination" type="text" name="redirects-to" value=""/><p slot="help">To redirect within the same domain, add a path: <em>/this-is-a-path</em>.</p>
              <p slot="help">To redirect to a different domain, add the full URL: <em>otherdomain.com/path</em>.</p>
            </res-input>
          </div>

          <div class="m-edit-redirect">
            <res-select>
              <label>Status</label>
              <select v-model="newStatus" name="status">
                <option>301</option>
                <option>302</option>
                <option>307</option>
                <option>308</option>
              </select>

            </res-select>
            <div slot="help" class="res-input__help-text">
              <p>The HTTP status code to use for the redirect. 301 is most common, but the following statuses are supported:</p>
              <ul>
                <li><strong>301 - Permanent</strong>, use with GET or HEAD requests.</li>
                <li><strong>302 - Temporary</strong>, use with GET or HEAD requests.</li>
                <li><strong>307 - Temporary</strong>, use with POST, PATCH, PUT, or DELETE requests.</li>
                <li><strong>308 - Permanent</strong>, use with POST, PATCH, PUT, or DELETE requests.</li>
              </ul>
            </div>
          </div>


          <SaveButton :enabled="true" @save="handleRedirectSave"/>

        </div>

      </main>
    </div>
  </div>
</template>


<script>
import SaveButton from './savebutton.vue';
import NetworkFilter from './NetworkFilter.vue';
import createRedirect from '../../lib/create_redirect';
import updateRedirect from '../../lib/update_redirect';

export default {
  name: 'Form',
  components: {
    SaveButton,
    NetworkFilter
  },
  props: {
    editContext: {
      type: String,
      required: true
    },
    networkId: {
      type: Number
    },
    communityId: {
      type: Number
    },
    status: {
      type: Number
    },
    fromPath: {
      type: String
    },
    destination: {
      type: String
    },
    redirectId: {
      type: Number
    }
  },
  data () {
    return {
      saveContext: '',
      creatingRedirect: false,
      redirectCreated: false,
      createError: false,
      saveErrorMessage: '',
      newStatus: this.status || 301,
      newPath: this.fromPath,
      newDestination: this.destination,
      newDomain: this.communityId,
      newNetwork: this.networkId,
      newDomainName: '',
      networks: this.$root.config.organization.networks,
    };
  },
  created() {
    this.newDomainName = this.networks
      .find(network => network.id === this.networkId).communities
      .find(community => community.id === this.communityId).domain;
  },
  methods: {
    updateCommunityAndNetworkId(communityId, networkId, domainName) {
      this.newDomain = communityId;
      this.newNetwork = networkId;
      this.newDomainName = domainName;
    },
    handleRedirectSave() {
      this.saveRedirect(this.newPath, this.newDestination, parseInt(this.newDomain), parseInt(this.newStatus), this.redirectId);
    },
    async saveRedirect(source, destination, community, status, redirectId) {
      this.creatingRedirect = true;
      this.redirectCreated = false;
      try {
        if (redirectId) {
          await updateRedirect(redirectId, source, destination, community, status);
          this.saveContext = 'edited';
        } else {
          await createRedirect(source, destination, community, status);
          this.saveContext = 'created';
        }
      } catch (err) {
        this.createError = true;
        this.creatingRedirect = false;
        console.error(err);
        // prints error message in the error banner
        this.saveErrorMessage = err.data[0].message;
        return;
      }

      this.creatingRedirect = false;
      this.redirectCreated = true;

      // wait a second to route user back to the dashboard, so they can see the success banner
      setTimeout(routeUserToDashboard, 1000);

      var self = this;
      function routeUserToDashboard() {
        self.$router.push({ path: '/admin/redirects', query: { community_id: self.newDomain, network_id: self.newNetwork } });
      }
    }
  }
};
</script>
