<template>
  <main class="m-dashboard__content m-entitlement-networks m-admin">
    <div>
      <router-link :to="{ name: 'entitlement-networks', params: { key: entitlementKey }}" class="m-entitlement-info">
        <res-icon class="m-back-icon" name="back" size="large"/>
        <p class="m-back-text">Back to networks</p>
      </router-link>
    </div>
    <h1 class="m-entitlement-name">{{ entitlementName }}</h1>
    <div class="m-entitlement-global-description-block">
      <p v-show="description" class="entitlement-description">{{ description }}</p>
      <res-switch
        :checked="isGlobal"
        :disabled="globalLoading"
        class="m-global-toggle"
        @change="toggleGlobal"
      >
        <label>Global</label><input type="checkbox" name="s1" />
      </res-switch>
    </div>
    <div v-show="initialLoading && !isGlobal">
      <res-icon class="m-loading-icon" name="loading" size="large" />
      <p class="m-loading-text">Loading entitlement’s communities</p>
    </div>
    <div v-if="!isGlobal && !initialLoading" class="m-entitlement-assignments">
      <res-input class="m-assignment-search">
        <input v-model="query" type="search" placeholder="Search communities" />
        <res-icon slot="inner-right" name="search" />
      </res-input>
      <h2 class="m-available-header">{{ network.name }}’s communities</h2>
      <res-table
        :mobile="isMobile"
        class="m-admin__table"
        zebra
      >
        <div class="m-dashboard__sub-head">
          <div class="m-dashboard__sub-head__title">
            <table>
              <thead>
                <tr>
                  <th class="m-table-entitlement-communities">Community</th>
                  <th class="m-table-entitlement-communities">Enabled</th>
                </tr>
              </thead>
              <tbody>
                <entitlement-assignment
                  v-for="(assignment, index) in paginatedAssignments"
                  v-bind="assignment"
                  :entitlementKey="entitlementKey"
                  :faviconUrl="communities.find(({ id }) => id === assignment.communityId).faviconUrl"
                  :key="`${page}-${index}`"
                />
              </tbody>
            </table>
          </div>
        </div>
      </res-table>
      <footer class="m-dashboard__footer m-admin__footer">
        <div
          v-if="pagination.total > 0"
          class="m-dashboard__footer__pagination m-admin__footer__pagination"
        >
          <res-pagination
            id="admin-pagination"
            :count="pagination.total"
            :display="pagination.perPage"
            :ticks="ticks"
            :page="pagination.page"
            indicator="page"
            @click="paginate()"
          />
        </div>
      </footer>
    </div>
  </main>
</template>

<script>
import EntitlementAssignment from '../../../components/entitlement-assignments.vue';
import * as entitlementActions from '../../../actions/entitlements.js';
import { getNetworkCommunities, getAllNetworks } from '../../../actions/communities';
import parsePagination from '../../../../client/lib/parse_pagination.js';
const MOBILE_WIDTH = 800;

export default {
  components: {
    EntitlementAssignment
  },
  data() {
    return {
      entitlementKey: 'entitlement_key',
      entitlementName: 'Entitlement Name',
      description: '',
      isGlobal: false,
      assignments: [],
      isMobile: false,
      page: 1,
      perPage: 36,
      ticks: 7,
      initialLoading: true,
      globalLoading: false,
      query: '',
      network: null,
      communities: []
    };
  },
  computed: {
    filteredAssignments() {
      if (!this.query) {
        return this.assignments;
      }

      return this.assignments.filter(a => {
        if (a.communityName.toLowerCase().includes(this.query.toLowerCase()) || a.communityId.toString().startsWith(this.query)) {
          return a;
        }
      });
    },
    pagination() {
      return parsePagination({
        page: this.page,
        perPage: this.perPage,
        total: this.filteredAssignments.length
      });
    },
    paginatedAssignments() {
      const start = (this.pagination.page - 1) * this.pagination.perPage;
      const end = start + this.pagination.perPage;
      return this.filteredAssignments.slice(start, end);
    }
  },
  async created() {
    const networks = await getAllNetworks();
    this.network = networks.find(network => network.id === parseInt(this.$route.params.id, 10));
    this.communities = await getNetworkCommunities(this.$route.params.id);
    this.entitlementKey = this.$route.params.key;
    await this.getEntitlementAssignmentsData();
  },
  beforeMount() {
    this.isMobile = window.innerWidth <= MOBILE_WIDTH;
    window.addEventListener('resize', () => this.isMobile = window.innerWidth <= MOBILE_WIDTH);
  },
  methods: {
    async getEntitlementAssignmentsData() {
      // Load in entitlement data
      let entitlementData = await entitlementActions.getEntitlementData(this.entitlementKey);
      this.entitlementName = entitlementData.values.name;
      this.description = entitlementData.values.description;
      this.isGlobal = entitlementData.isGlobal;

      // Load in community assignments
      this.assignments = await entitlementActions.getCommunityAssignments(this.entitlementKey, this.communities);
      this.globalLoading = false;
      this.initialLoading = false;
    },
    async toggleGlobal() {
      this.globalLoading = true;
      if (this.isGlobal) {
        try {
          await entitlementActions.unassignEntitlement({
            key: this.entitlementKey,
            global: true
          });
          this.isGlobal = !this.isGlobal;
          await this.getEntitlementAssignmentsData();
        } catch (err) {
          console.error(err);
        }
      } else {
        try {
          await entitlementActions.assignEntitlement({
            key: this.entitlementKey,
            global: true
          });
          this.isGlobal = !this.isGlobal;
        } catch (err) {
          console.error(err);
        }
      }
      this.globalLoading = false;
      this.initialLoading = false;
    },
    paginate() {
      this.page = document.getElementById('admin-pagination').page;
    }
  }
};
</script>
