<template>
  <div class="m-redirects__network-filter">
    <res-select>
      <label>Network</label>
      <select v-model="selectedNetworkId" name="select-network">
        <option placeholder="">Select network</option>


        <option v-for="network in networks" :key="network.id" :value="network.id">{{ network.name }}</option>


      </select>
    </res-select>

    <res-select style="margin-right: 8px;">
      <label>Domain</label>
      <select v-model="selectedCommunityId" name="select-community"><option placeholder="">Select community</option>

        <option v-for="community in currentNetwork.communities" :key="community.id" :value="community.id">{{ community.domain }}</option>

      </select>
    </res-select>
  </div>
</template>

<script>

export default {
  name: 'NetworkFilter',
  props: {
    networks: Array,
    networkId: Number,
    communityId: Number
  },
  data () {
    return {
      selectedNetworkId: this.networkId,
      selectedCommunityId: this.communityId || this.networks.find(function(network) {
        return network.id === parseInt(this.networkId);
      }, this).communities[0].id,
    };
  },
  computed: {
    // this returns the current network object as selected in the network dropdown
    currentNetwork: function() {
      return this.networks.find(function(network) {
        return network.id === this.selectedNetworkId;
      }, this);
    },
    selectedDomainName: function() {
      return this.currentNetwork.communities.find(function(community) {
        return community.id === this.selectedCommunityId;
      }, this).domain;
    }
  },
  watch: {
    // we need to watch when the current network changes using the network dropdown, and re-set the community id appropriately
    // for example, if someone changes the domain in the host dropdown, and then changes the network, we update the host dropdown to reflect the list of domains from the network they selected
    currentNetwork: function() {
      this.selectedCommunityId = this.currentNetwork.communities[0].id;
    },
    selectedCommunityId: function() {
      this.$emit('community-changed', this.selectedCommunityId, this.selectedNetworkId, this.selectedDomainName);
    }
  },
};
</script>
