<template>
  <tr v-show="communityName" class="m-dashboard__table__row">
    <td class="m-dashboard__table__assignments">
      <img v-if="faviconUrl" :src="faviconUrl" class="m-entitlement-community-icon" />
      <img v-else :src="this.$root.config.defaultLogoUrl" class="m-entitlement-community-icon" />
      <span class="m-community-name">{{ communityName }}</span>
    </td>
    <td class="m-entitlement-checked">
      <res-switch :checked="isEnabled" :disabled="loading" @change="toggleAssignment">
        <label class="m-entitlement-checked-label" />
        <input type="checkbox" name="s1" />
      </res-switch>
    </td>
  </tr>
</template>

<script>
import * as entitlementActions from '../actions/entitlements.js';

export default {
  props: {
    communityId: {
      type: Number,
      required: true
    },
    enabled: {
      type: Boolean,
      required: true,
      default: false
    },
    communityName: {
      type: String,
      required: true,
      default: ''
    },
    entitlementKey: {
      type: String,
      required: true,
      default: ''
    },
    faviconUrl: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      isEnabled: false
    };
  },
  created() {
    this.isEnabled = this.enabled;
  },
  methods: {
    async toggleAssignment() {
      this.loading = true;
      if (this.isEnabled) {
        try {
          await entitlementActions.unassignEntitlement({
            key: this.entitlementKey,
            communityId: this.communityId
          });
          this.isEnabled = !this.isEnabled;
        } catch (err) {
          console.error(err);
        }
      } else {
        try {
          await entitlementActions.assignEntitlement({
            key: this.entitlementKey,
            communityId: this.communityId
          });
          this.isEnabled = !this.isEnabled;
        } catch (err) {
          console.error(err);
        }
      }
      this.loading = false;
    }
  }
};
</script>
