import Vue from 'vue';
import App from './app.vue';
import router from './router/index';
import store from './store/index';
import '../shared/vue/lib/sentry_client';
import { apolloProvider } from '../shared/vue/lib/apollo_client';

new Vue({
  router,
  store,
  apolloProvider,
  ignoredElements: res.manifest.tags.all, // eslint-disable-line no-undef
  data() {
    let config = document.querySelector('script[type="config"]');
    return { config: JSON.parse(config.textContent) };
  },
  render: h => h(App)
}).$mount('#app');
