import graphqlRequest from '../../client/lib/graphql_request';

export default async function importRedirects(csv) {
  await graphqlRequest('/api/tower/graphql', {
    query: `
      mutation ImportRedirects($csv: String!) {
        importRedirects(input: { csv: $csv }) {
          importQueued
          recipientEmail
        }
      }
    `,
    variables: JSON.stringify({ csv: csv })
  });
  return;
}
