import graphqlRequest from '../../client/lib/graphql_request';

export default async function createRedirect(source, destination, community, status) {
  await graphqlRequest('/api/tower/graphql', {
    query: `
      mutation createRedirectInput($path: String!, $destination: String!, $communityId: Int!, $status: Int!) {
        createRedirect(input: {path: $path, destination: $destination, community: { id: $communityId }, status: $status}) {
          redirect {
            id
          }
        }
      }
    `,
    variables: JSON.stringify({ path: source, destination: destination, communityId: community, status: status })
  });
  return;
}
