import { sortBy } from 'lodash';
import request from '../../client/lib/request';
import { getCommunityData } from './communities';

export async function getEntitlements() {
  let exported = await request('/api/entitlements/keys');
  // mapping key to entitlementKey due to key being a special, reserved attribute in Vue
  return exported.map(({ key, values, isGlobal }) => {
    return {
      entitlementKey: key,
      values,
      isGlobal
    };
  });
}

export async function getEntitlementData(entitlementKey) {
  return await request(`/api/entitlements/data/${entitlementKey}`);
}

export async function getCommunityAssignments(entitlementKey, communities) {
  let { assignments } = await request(`/api/entitlements/assignments/${entitlementKey}`);

  // Take all existing community-level assignments + add in disabled assignments
  // for all other communities
  let allAssignments = communities.map(({ id, name }) => {
    let assignment = assignments.find(a => a.communityId === id);
    return {
      communityId: id,
      communityName: name,
      enabled: assignment != null
    };
  });

  return sortBy(allAssignments, ({ communityName }) => communityName);
}

// Merging community data from tower with entitlement assignment data
export async function updateAssignmentsCommunityData(communityAssignments) {
  return await Promise.all(communityAssignments.map(async community => {
    let data = await getCommunityData(community.community_id);
    return {
      entitlementAssignment: community.entitlementAssignment,
      data: data.community,
      enabled: community.enabled
    };
  }));
}

export async function assignEntitlement(params) {
  return await request('/api/entitlements/assign', {
    method: 'PUT',
    body: params
  });
}

export async function unassignEntitlement(params) {
  return await request('/api/entitlements/unassign', {
    method: 'PUT',
    body: params
  });
}
