import graphqlRequest from '../../client/lib/graphql_request.js';
import { AdminCommunity, AdminAllNetworks, AdminNetworkCommunities } from '../queries/communities.js';

// Grab community data from tower by communityId
export async function getCommunityData(communityId) {
  return await graphqlRequest('/api/tower/graphql', {
    query: AdminCommunity,
    variables: JSON.stringify({ communityId })
  });
}

export async function getAllNetworks() {
  const { networks } = await graphqlRequest('/api/tower/graphql', {
    query: AdminAllNetworks
  });
  return networks;
}

export async function getNetworkCommunities(networkId) {
  async function getPage(page) {
    const response = await graphqlRequest('/api/tower/graphql', {
      query: AdminNetworkCommunities,
      variables: { networkId, page }
    });
    return response.searchCommunities;
  }

  let response = { totalCount: Infinity, results: [] };
  let communities = [];
  // eslint-disable-next-line no-restricted-syntax
  for (let page = 1; communities.length < response.totalCount; page++) {
    response = await getPage(page);
    communities = communities.concat(response.results);
  }
  return communities;
}
