<template>
  <div class="m-redirects__import">
    <div class="m-redirects__messaging">
      <res-banner v-show="importingRedirects"><span>Importing redirects...</span></res-banner>
      <res-banner v-show="redirectsCreated" design="positive"><span>Success! Your redirects were imported.</span></res-banner>
      <res-banner v-show="importError" design="negative"><span>Something went wrong. Try refreshing the page.</span></res-banner>
    </div>
    <div class="l-content">

      <main class="m-dashboard__content">
        <div class="m-dashboard__sub-head m-redirects__import-head">
          <div class="m-dashboard__sub-head__title">
            <res-button type="default" paddingless>
              <res-icon size="small" name="back"/>
              <router-link :to="{ path: '/admin/redirects', query: { community_id: communityId, network_id: networkId }}">Back to redirects</router-link>
            </res-button>
            <h1>Import redirects from CSV</h1>
          </div>
        </div>

        <div class="m-edit">

          <p><a href="https://docs.google.com/spreadsheets/d/1XCSlToWhdq832HLdJsLYzZSx6HYPqRFou8IMnr6rYrg/edit?usp=sharing">Download this template as a CSV</a>, or create your own. CSV files should include three columns with the following headers:</p>

          <ul>
            <li><strong>source</strong>: The URL or path you want to redirect away from.</li>

            <li><strong>destination</strong>: The URL or path you want to redirect to.</li>

            <li><strong>status</strong>: The HTTP status code. 301 is most common, and used for permanent redirects. 302 is used for temporary redirects. 307 and 308 can also be used, but are not common.</li>
          </ul>

          <p>If an existing redirect is found with the same source, but a different destination or status, it will be updated.</p>


          <div class="m-redirects__import--choose-file">

            <input id="import-redirects-button" type="file" style="display:none" @change="handleFileName"/>

            <res-button @click="handleFileSelect">
              <res-icon name="upload"/><button>Choose File</button>
            </res-button>

            <span id="chosen-file-name">No file chosen</span>

          </div>


          <div class="m-redirects__import--submit">

            <div class="m-redirects__import--submit-buttons">
              <res-button design="primary" @click="handleRedirectsImport"><button>Import</button></res-button>
              <div v-show="noFileChosen" class="m-redirects__import--no-file"><span><em>Choose a file!</em></span></div>
            </div>
          </div>
        </div>


      </main>
    </div>
  </div>
</template>

<script>
import SaveButton from './savebutton.vue';
import importRedirects from '../../lib/import_redirects';

export default {
  name: 'Import',
  components: {
    SaveButton
  },
  props: {
    communityId: Number,
    networkId: Number
  },
  data () {
    return {
      importingRedirects: false,
      redirectsCreated: false,
      importError: false,
      noFileChosen: false,
    };
  },
  methods: {
    handleRedirectsImport() {
      var input = document.getElementById('import-redirects-button');
      if (input.files[0]) {
        var reader = new FileReader();
        var self = this;
        reader.onload = function(evt) {
          self.importRedirects(evt.target.result);
        };
        reader.readAsText(input.files[0]);
      } else {
        this.noFileChosen = true;
      }
    },
    handleFileSelect() {
      var fileInput = document.getElementById('import-redirects-button');
      if (fileInput) {
        fileInput.click();
      }
    },
    handleFileName() {
      var fileName = document.getElementById('chosen-file-name');
      var fileInput = document.getElementById('import-redirects-button');
      fileName.innerHTML = fileInput.files[0].name;
    },
    async importRedirects(csv) {
      this.importingRedirects = true;
      this.redirectsCreated = false;
      try {
        await importRedirects(csv);
      } catch (err) {
        this.importError = true;
        this.importingRedirects = false;
        console.error(err);
        return;
      }
      this.importingRedirects = false;
      this.redirectsCreated = true;
      this.noFileChosen = false;

      // wait a second to route user back to the dashboard, so they can see the success banner
      setTimeout(routeUserToDashboard, 1000);

      var self = this;
      function routeUserToDashboard() {
        self.$router.push({ path: '/admin/redirects', query: { community_id: self.communityId, network_id: self.networkId } });
      }

    }
  }
};
</script>
