<template>
  <main class="m-dashboard__content m-admin">
    <h1 class="m-entitlement-name">Entitlements</h1>
    <h2 class="m-available-header">Available entitlements</h2>
    <res-input class="m-entitlement-search">
      <input v-model="query" type="search" placeholder="Search entitlements" />
      <res-icon slot="inner-right" name="search" />
    </res-input>
    <res-table :mobile="isMobile" class="m-admin__table" zebra>
      <div class="m-dashboard__sub-head">
        <div class="m-dashboard__sub-head__title">
          <table>
            <thead>
              <tr>
                <th class="m-table-entitlement-name">Name</th>
                <th class="m-table-entitlement-description">Description</th>
                <th class="m-table-entitlement-global">Global</th>
                <th class="m-table-entitlement-action" />
              </tr>
            </thead>
            <tbody v-if="!query">
              <entitlement-cell
                v-for="entitlement in paginatedEntitlements"
                :key="entitlement.values.name"
                v-bind="entitlement"
              />
            </tbody>
            <tbody v-else>
              <entitlement-cell
                v-for="entitlement in filteredEntitlements"
                :key="entitlement.values.name"
                v-bind="entitlement"
              />
            </tbody>
          </table>
        </div>
      </div>
    </res-table>
    <footer class="m-dashboard__footer m-admin__footer">
      <div v-if="pagination.total > 0 && !query" class="m-dashboard__footer__pagination m-admin__footer__pagination">
        <res-pagination
          id="admin-pagination"
          :count="pagination.total"
          :display="pagination.perPage"
          :ticks="ticks"
          :page="pagination.page"
          indicator="page"
          navigation="text"
          @click="paginate()"
        />
      </div>
    </footer>
  </main>
</template>

<script>
import EntitlementCell from './entitlement-cell.vue';
import parsePagination from '../../client/lib/parse_pagination';

const MOBILE_WIDTH = 800;

export default {
  components: {
    EntitlementCell
  },
  props: {
    entitlements: {
      type: Array,
      required: true,
      default: () => ([])
    }
  },
  data() {
    return {
      isMobile: false,
      page: 1,
      perPage: 36,
      ticks: 7,
      query: ''
    };
  },
  computed: {
    pagination() {
      return parsePagination({
        page: this.page,
        perPage: this.perPage,
        total: this.entitlements.length
      });
    },
    paginatedEntitlements() {
      const start = (this.pagination.page - 1) * this.pagination.perPage;
      const end = start + this.pagination.perPage;
      return this.entitlements.slice(start, end);
    },
    filteredEntitlements() {
      return this.entitlements.filter(e => {
        return e.values.name.toLowerCase().includes(this.query.toLowerCase());
      });
    }
  },
  beforeMount() {
    this.isMobile = window.innerWidth <= MOBILE_WIDTH;
    window.addEventListener('resize', () => this.isMobile = window.innerWidth <= MOBILE_WIDTH);
  },
  methods: {
    paginate() {
      this.page = document.getElementById('admin-pagination').page;
    }
  }
};
</script>
