<template>
  <main class="m-dashboard__content m-entitlement-networks m-admin">
    <div>
      <router-link :to="{ name: 'entitlements-dashboard' }" class="m-entitlement-info">
        <res-icon class="m-back-icon" name="back" size="large" />
        <p class="m-back-text">Back to entitlements</p>
      </router-link>
    </div>
    <h1 class="m-entitlement-name">{{ name }}</h1>
    <div class="m-entitlement-global-description-block">
      <p v-show="description" class="entitlement-description">{{ description }}</p>
      <res-switch
        :checked="isGlobal"
        :disabled="globalLoading"
        class="m-global-toggle"
        @change="toggleGlobal"
      >
        <label>Global</label><input type="checkbox" name="s1" />
      </res-switch>
    </div>
    <div v-show="initialLoading && !isGlobal">
      <res-icon class="m-loading-icon" name="loading" size="large" />
      <p class="m-loading-text">Loading {{ name }}’s networks</p>
    </div>
    <div v-if="!isGlobal && !initialLoading" class="m-entitlement-assignments">
      <h2 class="m-available-header">Networks</h2>
      <res-input class="m-assignment-search">
        <input v-model="query" type="search" placeholder="Search networks" />
        <res-icon slot="inner-right" name="search" />
      </res-input>
      <res-table :mobile="isMobile" class="m-admin__table" zebra>
        <div class="m-dashboard__sub-head">
          <div class="m-dashboard__sub-head__title">
            <table>
              <thead>
                <tr>
                  <th>Network</th>
                  <th class="m-dashboard__table__communities-header">Communities</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="network in filteredNetworks" :key="network.id" class="m-dashboard__table__row">
                  <td class="m-dashboard__table__networks">
                    <div>
                      <div v-if="network.svgLogo" class="m-dashboard__table__avatar" v-html="network.svgLogo" />
                      <img v-else :src="$root.config.defaultLogoUrl" class="m-dashboard__table__icon" />
                      <div class="m-network-name">{{ network.name }}</div>
                    </div>
                  </td>
                  <td class="m-dashboard__table__communities">
                    <router-link :to="{ name: 'entitlement-communities', params: { key: entitlementKey, id: network.id }}" class="m-community-info">
                      {{ network.searchCommunities.totalCount }} (See all)
                    </router-link>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </res-table>
    </div>
  </main>
</template>

<script>
import { sortBy } from 'lodash';
import * as entitlementActions from '../../../actions/entitlements.js';
import { getAllNetworks } from '../../../actions/communities';

const MOBILE_WIDTH = 800;

export default {
  data() {
    return {
      entitlementKey: '',
      name: '',
      description: '',
      isGlobal: false,
      isMobile: false,
      initialLoading: true,
      globalLoading: false,
      query: '',
      networks: []
    };
  },
  computed: {
    filteredNetworks() {
      if (!this.query) {
        return this.networks;
      }

      return this.networks.filter(a => {
        if (a.name.toLowerCase().includes(this.query.toLowerCase()) || a.id === parseInt(this.query)) {
          return a;
        }
      });
    }
  },
  async created() {
    this.networks = sortBy(await getAllNetworks(), 'name');
    this.entitlementKey = this.$route.params.key;
    await this.getEntitlementData();
  },
  beforeMount() {
    this.isMobile = window.innerWidth <= MOBILE_WIDTH;
    window.addEventListener('resize', () => this.isMobile = window.innerWidth <= MOBILE_WIDTH);
  },
  methods: {
    async getEntitlementData() {
      let entitlementData = await entitlementActions.getEntitlementData(this.entitlementKey);
      this.name = entitlementData.values.name;
      this.description = entitlementData.values.description;
      this.isGlobal = entitlementData.isGlobal;

      this.globalLoading = false;
      this.initialLoading = false;
    },
    async toggleGlobal() {
      this.globalLoading = true;
      if (this.isGlobal) {
        try {
          await entitlementActions.unassignEntitlement({
            key: this.entitlementKey,
            global: true
          });
          this.isGlobal = !this.isGlobal;
          await this.getEntitlementData();
        } catch (err) {
          console.error(err);
        }
      } else {
        try {
          await entitlementActions.assignEntitlement({
            key: this.entitlementKey,
            global: true
          });
          this.isGlobal = !this.isGlobal;
        } catch (err) {
          console.error(err);
        }
      }
      this.globalLoading = false;
      this.initialLoading = false;
    },
    paginate() {
      this.page = document.getElementById('admin-pagination').page;
    }
  }
};
</script>
