export const AdminCommunity = `
  query NetworkCommunity($communityId: Int!) {
    community(id: $communityId) {
      id
      name
      faviconUrl
      placeholderImageUrl
    }
  }
`;

export const AdminAllNetworks = `
  query AdminAllNetworks {
    networks {
      id
      name
      svgLogo: siteConfigString(key: "svg_small_mark_full_color")
      searchCommunities(query: { perPage: 0 }) {
        totalCount
      }
    }
  }
`;

export const AdminNetworkCommunities = `
  query AllNetworkCommunities($networkId: Any, $page: Int!) {
    searchCommunities(query: {
      whereAll: [{ field: "networkId", equal: $networkId }]
      publicOnly: false
      perPage: 100
      pageNumber: $page
    }) {
      totalCount
      results {
        id
        name
        faviconUrl
      }
    }
  }
`;
