<template>
  <div class="m-redirects__save">
    <res-button
      v-show="saveState === 'READY' || saveState === 'SAVED'"
      :disabled="!enabled"
      design="primary"
      @click="clickSave"
    ><button>Create redirect</button></res-button>

    <res-button
      v-show="saveState === 'CONFIRM'"
      design="primary"
      @click="clickConfirm"
    ><button>Confirm</button></res-button>
    <res-button
      v-show="saveState === 'CONFIRM'"
      negative=""
      @click="clickCancel"
    ><res-icon name="x"/><button>Cancel</button></res-button>
  </div>
</template>

<script>
export default {
  name: 'SaveButton',
  props: {
    enabled: Boolean,
  },
  data: function() {
    return {
      saveState: 'READY'
    };
  },
  watch: {
    enabled: function(val) {
      if (val) {
        this.saveState = 'READY';
      }
    }
  },
  methods: {
    clickSave: function() {
      this.saveState = 'CONFIRM';
    },
    clickConfirm: function() {
      this.saveState = 'SAVED';
      this.$emit('save');
    },
    clickCancel: function() {
      this.saveState = 'READY';
    }
  }
};
</script>
