import graphqlRequest from '../../client/lib/graphql_request';

export default async function deleteRedirect(Id) {
  await graphqlRequest('/api/tower/graphql', {
    query: `
      mutation DeleteRedirect($id: Int!) {
        deleteRedirect(where: {id: $id}) {
          redirect {
            id
          }
        }
      }
    `,
    variables: JSON.stringify({ id: Id })
  });
  return;
}
