<template>
  <div class="m-redirects__dashboard">
    <res-banner v-show="deletingRedirect"><span>Deleting redirect...</span></res-banner>
    <res-banner v-show="redirectDeleted" design="positive"><span>Success! Your redirect was deleted.</span></res-banner>
    <res-banner v-show="deleteError" design="negative"><span>Something went wrong. Try refreshing the page.</span></res-banner>
    <div class="l-content">
      <main class="m-dashboard__content">
        <div class="m-dashboard__sub-head p-flex m-redirects__dashboard-head">
          <div class="m-dashboard__sub-head__title">
            <h1>Redirects</h1>
          </div>
          <div>
            <router-link :to="{ path: '/admin/redirects/import', query: { community_id: community_id, network_id: network_id }}"><res-button>
            <res-icon name="upload"/><button>Import from CSV</button></res-button></router-link>
            <router-link :to="{ path: '/admin/redirects/create', query: { community_id: community_id, network_id: network_id }}"
            ><res-button design="primary"><button>Create</button></res-button></router-link>
          </div>
        </div>


        <div class="p-flex m-redirects__search">
          <networkFilter
            :networks="networks"
            :communityId="community_id"
            :networkId="network_id"
            @community-changed="updateCommunityAndNetworkId"
          />

          <res-input class="m-redirects__search--path"><label>Filter by path</label> <input v-model="path" type="text" name="search-path" /></res-input>

          <res-input class="m-redirects__search--destination"><label>Filter by destination</label> <input v-model="destination" type="text" name="search-destination" /></res-input>

        </div>


        <res-table>
          <table class="m-edit-links">
            <thead>
              <tr>
                <th>Domain</th>
                <th>Path</th>
                <th>Destination</th>
                <th>Status</th>
                <th/>
              </tr>
            </thead>


            <tbody>
              <tr v-for="redirect in searchRedirectsResults" :key="redirect.id" :data-id="redirect.id">
                <td>{{ redirect.host }}</td>
                <td>{{ redirect.path }}</td>
                <td>{{ redirect.destination }}</td>
                <td>{{ redirect.status }}</td>
                <td>
                  <div class="m-redirects__controls">
                    <router-link :to="{ path: '/admin/redirects/edit', query: { community_id: community_id, network_id: network_id, path: redirect.path, destination: redirect.destination, status: redirect.status, redirectId: redirect.id }}"
                    >
                      <res-button>
                      <res-icon name="edit"/><button>Edit</button></res-button>
                    </router-link>

                    <res-button :res-modal-open="redirect.id" negative="">
                      <res-icon name="trash"/><button>Delete</button>
                    </res-button>
                  </div>
                </td>
                <res-modal :id="redirect.id" close-on-bg-click="">
                  <div>
                    <p class="m-redirects__modal-warning">Are you sure you want to destroy this redirect?</p>
                    <res-button id="modal-close" :res-modal-close="redirect.id" design="secondary" @click="handleDelete(redirect.id)"><button>Delete redirect</button></res-button>
                    <res-button id="modal-close" :res-modal-close="redirect.id">
                    <res-icon name="x"/><button>Cancel</button></res-button>
                  </div>
                </res-modal>
              </tr>
            </tbody>
          </table>


        </res-table>
        <div class="p-flex m-redirects__pagination"><res-pagination :count="searchRedirects.totalCount"
                                                                    :display="perPage"
                                                                    :page="page"
                                                                    ticks="7"
                                                                    indicator="both"
                                                                    @click="updatePage($event)"/></div>
      </main>
    </div>
  </div>
</template>

<script>
import redirectsDataQuery from '../../queries/redirects.graphql';
import NetworkFilter from './NetworkFilter.vue';
import deleteRedirect from '../../lib/delete_redirect';

export default {
  name: 'Redirects',
  components: {
    NetworkFilter
  },
  props: {
    community: Object
  },
  data () {
    return {
      searchRedirects: { results:[], totalCount: '' },
      perPage: 30,
      page: 1,
      pageBeforeFiltering: 1,
      query: '',
      path: '',
      destination: '',
      network_id: this.community.network.id,
      community_id: this.community.id,
      networks: this.$root.config.organization.networks,
      deletingRedirect: false,
      redirectDeleted: false,
      deleteError: false,
    };
  },
  computed: {
    searchRedirectsResults() {
      return this.searchRedirects.results;
    }
  },
  watch:{
    $route () {
      // so the data refreshes if the user navigates back from the create/edit pages
      this.$apollo.queries.searchRedirects.refetch();
    },
    path () {
      this.handleFilterPage();
    },
    destination () {
      this.handleFilterPage();
    }
  },
  beforeMount() {
    this.setInitialData();
  },
  methods: {
    async setInitialData() {
      // set data from the querystring if it exists
      let queryParams = this.$route.query;
      this.page = parseInt(queryParams.page, 10) || 1;
      this.path = queryParams.path || '';
      this.destination = queryParams.destination || '';
      this.community_id = parseInt(queryParams.community_id) || this.community_id;
      this.network_id = parseInt(queryParams.network_id) || this.network_id;
    },
    async handleDelete(id) {
      this.deletingRedirect = true;
      this.redirectDeleted = false;
      try {
        await deleteRedirect(id);
      } catch (err) {
        this.deleteError = true;
        this.deletingRedirect = false;
        console.error(err);
        return;
      }

      await this.$apollo.queries.searchRedirects.refetch();
      this.deletingRedirect = false;
      this.redirectDeleted = true;
    },
    updatePath() {
      // this updates the URL path when the network filter selects a different community
      // it also updates the path upon using the pagination bar and the filter by path/destination fields
      this.$router.push({ query: { page: this.page, community_id: this.community_id, network_id: this.network_id, path: this.path, destination: this.destination } }).catch(()=>{});
    },
    updateCommunityAndNetworkId(communityId, networkId) {
      this.community_id = communityId;
      this.network_id = networkId;
    },
    handleFilterPage() {
      // if we use the filter, the page number should get set to 1, but only once
      if (this.page === 1) {
        // check if the filter is empty
        if (this.path === '' && this.destination === '') {
          // if the filter is empty, set the page to the page the user was on
          this.page = this.pageBeforeFiltering;
        }
      } else {
        this.pageBeforeFiltering = this.page;
        this.page = 1;
      }
    },
    updatePage(e) {

      // if we click prev, page number minus one

      if (e.target.dataset.direction === 'prev') {
        this.page = this.page - 1;
      }

      // if we click next, page number plus one

      if (e.target.dataset.direction === 'next') {
        this.page = this.page + 1;
      }

      // if we click a number, set page number to that number

      if (e.target.dataset.page) {
        this.page = parseInt(e.target.dataset.page);
      }
    }
  },
  apollo: {
    searchRedirects: {
      query: redirectsDataQuery,
      variables() {
        return {
          network_id: this.network_id,
          pageNumber: this.page,
          perPage: this.perPage,
          community_id: this.community_id,
          path: this.path || null,
          destination: this.destination || null
        };
      },
      result( { loading, error } ) {
        if (!loading) {
          if (error) {
            console.log(`Error: ${error.gqlError.message}`);
          } else {
            this.updatePath();
          }
        }
      }
    },
  },
};
</script>
