<template>
  <div class="m-nav--assets">
    <div class="p-flex--v m-nav--assets">
      <redirects v-if="community" :community="community"/>
    </div>
  </div>
</template>

<script>
import Redirects from '../../../components/redirects/redirects.vue';

export default {
  components: {
    Redirects,
  },
  props: {
    communityId: Number,
    networkId: Number
  },
  data() {
    return {
      // the app needs to start with some initial community/network data -- this is defaulting to Vox
      community: {
        id: this.communityId || this.$root.config.organization.primaryNetwork.primaryCommunity.id,
        network: {
          id: this.networkId || this.$root.config.organization.primaryNetwork.id
        }
      },
    };
  },
  created() {
    document.title = 'Redirects | ' + this.$root.config.appName;
  }
};
</script>
