import Vue from 'vue';
import VueRouter from 'vue-router';
import EntitlementsDashboard from './views/entitlements/entitlement-dashboard.vue';
import EntitlementNetworks from './views/entitlements/entitlement-networks.vue';
import EntitlementCommunities from './views/entitlements/entitlement-communities.vue';
import Dashboard from './views/redirects/dashboard.vue';
import Edit from '../components/redirects/edit.vue';
import Create from '../components/redirects/create.vue';
import Import from '../components/redirects/import.vue';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/admin',
      redirect: '/admin/entitlements'
    },
    {
      path: '/admin/entitlements',
      name: 'entitlements-dashboard',
      component: EntitlementsDashboard
    },
    {
      path: '/admin/entitlements/:key',
      name: 'entitlement-networks',
      component: EntitlementNetworks
    },
    {
      path: '/admin/entitlements/:key/network/:id',
      name: 'entitlement-communities',
      component: EntitlementCommunities
    },
    {
      path: '/admin/redirects',
      name: 'Redirects',
      component: Dashboard,
      props: route => ({
        communityId: parseInt(route.query.community_id),
        networkId: parseInt(route.query.network_id)
      })
    },
    {
      path: '/admin/redirects/edit',
      name: 'Edit',
      component: Edit,
      props: route => ({
        communityId: parseInt(route.query.community_id),
        networkId: parseInt(route.query.network_id),
        fromPath: route.query.path,
        destination: route.query.destination,
        status: parseInt(route.query.status),
        redirectId: parseInt(route.query.redirectId)
      })
    },
    {
      path: '/admin/redirects/create',
      name: 'Create',
      component: Create,
      props: route => ({
        communityId: parseInt(route.query.community_id),
        networkId: parseInt(route.query.network_id)
      })
    },
    {
      path: '/admin/redirects/import',
      name: 'Import',
      component: Import,
      props: route => ({
        communityId: parseInt(route.query.community_id),
        networkId: parseInt(route.query.network_id)
      })
    }
  ]
});
export default router;
